<template>
  <div class="search_box">
    <div class="search_bar">
      <div class="search_bar-cont">
        <form action="#" method="get">
          <input  v-model="val"  type="text" class="search_input" placeholder="搜索歌曲、歌单、专辑" />
          <span class="search_icon" @click='golist()'>搜索</span>
        </form>
      </div>
      <div class="search_cal" v-show="flag" @click="cancel()">取消</div>
    </div>
    <div class="hot_search" v-show="!flag">
      <h3 class="hot_title">热门搜索</h3>
      <div class="result_tags">
        <div @click='golist(item.first)'  :class='[i==0 ? "red":""]' href="javascript:;" class="tag_s" v-for="(item,i) in hotName" :key="i">{{item.first}}</div>
       </div>
    </div>
    <div class="search_record" v-show="flag">
      <ul class="songlist">
        <li class="list_item" v-for="(its) in songlist" :key='its.id' @click='goPlay(its.id)'>
          <div class="item_box">
            <div class="item_bd">
              <h3 class="title txt">{{its.name}}</h3>
              <p class="song_desc txt clearfix">
                <span class="list_txt">{{its.artists[0].name}}</span>
              </p>
            </div>
          </div>
        </li>
        
      </ul>
    </div>
  </div>
</template>
<script>
//引入接口api文件
import api from '../../common/api'
export default {
  data() {
    return {
      hotName: [],
      flag: false,
      songlist:[],
      val:'',
    };
  },
  methods: {
    show() {
      this.flag = true;
       songlist:[];
    },
    cancel() {
      this.flag = false;
       songlist:[];
    },
    golist(name){
      this.flag=true;
      this.$http({
          url:'http://localhost:3000/search',
          params:{
            keywords: name ||this.val,
          }
         })
          .then(res => {
            // console.log(res, "歌曲列表11111");
            this.songlist=res.data.result.songs;
          })
          .catch(err => {
            console.log(err, "失败");
      });
      this.val='';
    },
    //点击每一个列表，传歌曲id，跳到播放页面
    goPlay(id){
      this.$router.push(
        {
            path:'/play',
            query:{
              id   
            }
        } )
    }
 
  },
  mounted() {
    //页面一进来就获取热门搜索内容
    this.$http({
      url:api.searchhot
    })
      .then(res => {
       this.hotName = res.data.result.hots;
      })
      .catch(err => {
        console.log(err, "失败");
      });
  }
};
</script>
<style lang="" scoped>
@import "../../assets/css/search.css";
.red{
  color: red
}
.songlist{
  overflow: hidden;
}
.songlist .list_item{
  position: relative;
  margin-bottom: .16rem;
  padding: 0 .32rem;
}
.songlist .list_item .item_box{
    display:flex;
    -webkit-box-align: center;
    min-height: .82rem;
}
.songlist .list_item .item_box .item_bd{
  flex: 1;
}
.songlist .list_item .item_box .item_bd .txt{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width:3.75rem;
}
.songlist .list_item .item_box .item_bd .title {
    color: rgba(26,26,26,1);
    line-height: .4rem;
    font-size: .32rem;
    font-weight: 400;
}
.songlist .list_item .item_box .item_bd .song_desc{
     color: rgba(26,26,26,.5);
     margin-top: .12rem;
     line-height: .32rem;
     font-weight: 300;
    
}
.songlist .list_item .item_box .item_bd .song_desc .list_txt{
  display:block;
  float: left;
  margin-right: .08rem;
  line-height: .32rem;
  font-size: .24rem;
}

</style>


