<template>
  <div class="mod_topic">
    <ul>
      <li class="topic_item" v-for="item in chartsArr" :key="item.id" @click="golist()">
        <div class="top_main">
          <a href="javascript:;" class="top_media">
            <img :src="item.coverImgUrl" />
            <span class="listen_count">
              <i class="icon_listen"></i>
              {{item.playCount}}万
            </span>
          </a>
          <div class="top_info">
            <div class="top_cont">
              <h3 class="top_tit">{{item.name}}</h3>
              <p v-for="(items,i) in item.tracks" :key="i">
                {{i+1}}
                <span class="text_name">{{items.first}}</span>
                {{items.second}}
              </p>
            </div>
            <i class="topic_arrow"></i>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '../../common/api'
export default {
  data() {
    return {
      chartsArr: [
        /*{
          id: 1,
          imgSrc: require("../../assets/images/wangluo.jpg"),
          num: "920.0万",
          title: "网络歌曲榜",
          topArr: [
            {
              songname: "嚣张",
              singer: "- en"
            },
            {
              songname: "从前的我快乐过",
              singer: "- 哼哼是李韩宇"
            },
            {
              songname: "潮湿的心",
              singer: "- 小阿枫"
            }
          ]
        }*/
      ]
    };
  },
  mounted() {
    //获取电台数据
    this.$http({
      url: api.rank
    })
      .then(res => {
        //   console.log(res,'排行榜11')
         this.chartsArr = res.data.list;
      })
      .catch(err => {
        console.log(err, "失败");
      });
  },
  methods: {
      golist(){
        //   点击排行榜中的各个榜单，都跳转到列表页面
          this.$router.push('/songlist')
      }
  },
};
</script>
<style scoped>
@import "../../assets/css/charts.css";
</style>


