<template>
  <div>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-if="4>i" v-for="(item,i) in imgSrc" :key="i">
          <img :src="item.imageUrl" alt />
        </div>
      </div>
      <div class="swiper-pagination"></div>
    </div>

    <!-- 电台 -->
    <div class="bgc">
      <div class="twocol_list">
        <h2 class="list_title">电台</h2>
        <ul class="list_container clearfix">
            <!-- v-if="2>i" -->
          <li  v-for="(item,i) in songLists" :key="item.id" @click="goDetail(item.id)">
            <a  class="list_main">
              <div class="list_media">
                <img :src="item.picUrl" alt class="list_media_img" />
                <span class="icon icon_play"></span>
              </div>
              <div class="list_info">
                <h3 class="list_tit">{{item.name}}</h3>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <!-- 底部 -->
    <div class="footer">
      <div class="vesion_tab">
        <a href="https://y.qq.com/?ADTAG=myqq&nomobile=1#type=index">查看电脑版网页</a>
      </div>
      <div class="footer_logo">QQ音乐</div>
      <p class="copyright">
        Copyright © 1998 -
        <span>2019</span> Tencent. All Rights Reserved.
      </p>
      <p class="copyright">联系电话：0755-86013388 QQ群：55209235</p>
    </div>
  </div>
</template>
<script>
//局部引入swiper
import Swiper from "swiper";
//引入api文件
import api from '../../common/api'
 
export default {
  data() {
    return {
      imgSrc: [],
      songLists: []
    };
  },
  methods: {
    getSwiper() {
      new Swiper(".swiper-container", {
        //设置分页器
        pagination: {
          el: ".swiper-pagination"
        },
        paginationClickable: true,
        paceBetween: 30,
        centeredSlides: true,
        autoplay: true,
        autoplayDisableOnInteraction: false,
        loop: true
      });
    },
    goDetail(id){
      this.$router.push({
        path:'/play',
        query:{
          id
        }
      })
    }
  },
  mounted() {
    //获取banner图数据
    this.$http({
      url: api.banner
    })
      .then(res => {
       
        this.imgSrc = res.data.banners;
        this.$nextTick(() => {
          this.getSwiper();
        });
      })
      .catch(err => {});
    //获取电台数据
    this.$http({
      url:api.djprogram
    })
      .then(res =>{ 
        console.log(res,'res')
        this.songLists = res.data.result;
      })
      .catch(err => {
        console.log(err, "失败");
      });
  }
};
</script>

<style lang="" scoped>
@import "../../assets/css/recommend.css";
</style>


