import Vue from 'vue'
import Router from 'vue-router'
//引入一级路由组件
import mPublic from '@/components/public'
import Play from '@/components/play'
import Play2 from'@/components/play2'
//引入二级路由组件
import Recommend from '@/components/pages/recommend'
import Rank from '@/components/pages/rank'
import Search from '@/components/pages/search'


Vue.use(Router)
const router=new Router({
  mode:'history',
  routes: [
    {
      path:'/public',
      component:mPublic,
      children:[
        {
          path:'recommend',
          component:Recommend
        },
        {
          path:'rank',
          component:Rank
        },{
          path:'search',
          component:Search
        },
      ], 
      //当输入/public时重定向到推荐页
      redirect:'/public/recommend'
    },
    {
      path:'/play',
      component:Play
    },
    {
      path:'/play2',
      component:Play2
    }
     ,{
      path:'*',
      redirect:'/public/recommend'
    }
  ],
  scrollBehavior(to,from,savedPosition){
      if(savedPosition){
        return savedPosition
      }else {
        return {
          x:0,
          y:0
        }
      }
  }
})

export default  router


