<template>
  <div>
    <div class="top_box">
      <div class="top_bd">
        <img :src="imgUrl" alt="QQ音乐logo" />
        <a href="javascript:;" class="down_btn">下载APP</a>
      </div>
    </div>
    <div class="nav">
      <router-link to="/public/recommend" activeClass="active">推荐</router-link>
      <router-link to="/public/rank" activeClass="active">排行榜</router-link>
      <router-link to="/public/search" activeClass="active">搜索</router-link>
    </div>
    <!-- 渲染子路由组件 -->
    <router-view></router-view>
    <div class="bottom_bar">
      <a href="javascript:;" class="bar_logo">安装QQ音乐 发现更多精彩</a>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imgUrl:
        "https://y.gtimg.cn/mediastyle/mod/mobile/img/logo_ch.svg?max_age=2592000"
    };
  }
};
</script>
<style lang="">
.active {
  color: aquamarine;
  border-bottom: 2px solid green;
}
</style>


