<template>
  <div>
    <!-- 头部 -->
    <div class="top_box">
      <div class="top_logo">
        <img :src="logoUrl" class="logo_img">
        <div class="txt_desc">
            <h3>你的好友</h3>
            <p>在用 QQ音乐  邀你一起</p>
        </div>
        <div  class="all_use">一直使用</div>
      </div>
    </div>
    <div class="main_box">
        <div class="song_info">
            <h2 class="song_name">{{detail.name}}</h2>
            <h2 class="singer_name">{{detail.ar[0].name}}</h2>
        </div>
        <div class="song_info_bd">
            <div class="cover_img">
              <img :src="detail.al.picUrl" alt=""  >
            </div>
        </div>
        <div class="play_box">
            <audio  :src="playUrl" class="play" controls autoplay ref="audioPlay"></audio>
            <div class="controlBox">
              <span class=""><i class=""></i></span><span class="go"><i></i></span> <span class="back"><i></i></span>
            </div>
        </div>
    </div>
    <div class="word" v-if="songword">
      {{songword}}
    </div>
  </div>
</template>
<script>
//引入
import  VueAplayer from 'vue-aplayer'
export default {
  name:'Aplayer',
  props:['pdfurl'],
  components:{
    'a-player':VueAplayer
  },
  data() {
    return {
      logoUrl: require("../../src/assets/images/play_logo.png"),
      playUrl:'',
      detail:'',
      songword:'',
    }
  },
  methods: {
    init(){
        //  console.log(this.$route.query.id,'idddd')  
          //根据歌曲id地址,获取音频播放地址mp3
      this.$http({
        url:'http://localhost:3000/song/url',
        params:{
            id:this.$route.query.id
        }
      })
        .then(res =>{
          this.playUrl=res.data.data[0].url
        })
        .catch(err => {
          console.log(err, "失败");
        });

      //根据歌曲id地址,歌曲详情信息
      this.$http({
        url:'http://localhost:3000/song/detail',
        params:{
            ids:this.$route.query.id
        }
      })
        .then(res =>{
            this.detail=res.data.songs[0]
        })
        .catch(err => {
          console.log(err, "失败");
        });

      //根据歌曲id地址,获取歌词
      this.$http({
        url:'http://localhost:3000/lyric',
        params:{
            id:this.$route.query.id
        }
      })
        .then(res =>{
            // console.log(res,'歌词')
            this.songword=res.data.lrc.lyric
            // console.log(this.songword,'歌词')
        })
        .catch(err=> {
          console.log(err,"失败");
        });
    }
  },
  mounted(){
    this.init();
  },
  activated() { //解决keep-alive缓存不调取接口
    this.init()
    console.log(this.$refs.audioPlay,'ref')
    let play=this.$refs.audioPlay


  },
};
</script>
<style lang="" scoped>
    .top_box{
        height:1.6rem;
    }
 .top_box .top_logo{
    height: 1.6rem;
    padding: 0 .2rem;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 100;
    display: -webkit-box;
    -webkit-box-pack: justify;
    -webkit-box-align: center;
    box-shadow: 0 0.05rem 0.05rem rgba(0,0,0,.02);
    background: #fff;
}
 .top_box .top_logo img{
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    margin-right: .2rem;
    border: 0;
}
.top_box .top_logo .txt_desc{
    -webkit-box-flex: 1;
}
.top_box .top_logo .txt_desc h3{
    font-size: .32rem;
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.top_box .top_logo .txt_desc p{
    font-size: .26rem;
}
.top_box .top_logo .all_use{
    min-width: .8rem;
    height: .5rem;
    line-height: .5rem;
    padding: 0 .2rem;
    text-align: center;
    font-size: .28rem;
    color: #000;
    border: solid 1px rgba(0,0,0,.3);
    border-radius: 1rem;
}
.main_box{
    position: relative;
    padding-bottom: .6rem;
    overflow: hidden;
}
.song_info .song_name{
    text-align: center;
    margin-top: .4rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: .38rem;
    font-weight: 400;
    color: #000;
}
.song_info .singer_name{
    margin: 0 .4rem 0.3rem;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: .28rem;
    font-weight: 300;
}

.song_info_bd{
    position: relative;
    height: 4.68rem;
}
.song_info_bd .cover_img{
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 2;
    width: 4.68rem;
    height: 4.68rem;
    margin-left: -2.34rem;
    box-shadow: 0 0.1rem 0.4rem rgba(0,0,0,.2);
}
.song_info_bd .cover_img img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.play_box{
    height: 1.8rem;
}
.play_box .play{
width: 100%;
height: 0.9rem;
}
.play_box .controlBox{

}
.word{
  height: 3.2rem;
  background-color: #fff;
  border: 1px solid gray;
  color: red;
  font-size:0.16rem;
}
  
</style>


